<template>
  <div class="echart" id="mychart" :style="myChartStyle"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      xData: ["九宫微格", "神仙豆腐", "扬眉吐气", "心如止水", "青春不老", "软谈丽语", "成宁微格","燕浩微格","芬芳馥郁","乡邻乡亲","网络家人","云端在手","一枝独秀","左邻右舍","抚琴谈心","不拒一格","仙悦微格","幸福家园","千禧微格"], //横坐标
      yData: [100, 140, 235, 100, 134, 120, 100,200,150,151,190,170,180,160,210,230,150,162,188], //数据
      myChartStyle: { float: "left", width: "100%", height: "310px" } //图表样式
    };
  },
  mounted() {
    this.initEcharts();
  },
  methods: {
    initEcharts() {
      // 基本柱状图
      const option = {
        xAxis: {
          axisLabel: {
            interval: 0,
            formatter: function (value) {
              return value.split("").join("\n");
            },
          },
          data: this.xData
        },
        yAxis: {},
        series: [
          {
            type: "bar", //形状为柱状图
            color: "#ffc400",
            data: this.yData
          }
        ]
      };
      const myChart = echarts.init(document.getElementById("mychart"));
      myChart.setOption(option);
      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    }
  }
};
</script>



