<!--main-->
<template>
  <div>
    <v-chart
      ref="ref_echart1"
      :option="option_column"
      style="height: 300px"
    ></v-chart>
  </div>
</template>
<script>
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import {initData} from "../../api/inidata";
export default {
  data() {
    return {
      eventData:{},
      option_column: {
      },
    };
  },
  created() {
      this.getEventData()
  },
  mounted() {
    window.addEventListener("resize", () => this.$refs.ref_echart1.resize());
  },
  methods: {
    getEventData() {
      initData('http://47.100.166.173:8099/qujiangqu/api/event/statistics/stauts').then(res => {
        this.eventData=res.data
        this.option_column= {
          tooltip: {},
          series: [
            {
              name: "事件上报",
              type: "pie",
              color : [ '#00E676', '#FF9100','#00e5ff'],
              data: [
                {
                  value: this.eventData.data['已答复'],
                  name: "已答复"
                },
                {
                  value: this.eventData.data['已上报'],
                  name: "已上报"
                },
                {
                  value: this.eventData.data['已办结'],
                  name: "已办结"
                }
              ],
              label:{
                normal:{
                  textStyle:{
                    color: '#fff',
                    fontSize:'20px'
                  },
                  formatter: '{b}',
                }
              },

            },
          ],
        }
      })
    }
  },
};
</script>



