<template>
  <div id="index">
    <dv-full-screen-container class="bg">
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div class="header-box">
        <div class="time-box">
          <div class="time">{{ time }}</div>
          <div style="margin-left: 1rem">
            <div class="date">{{ date }}</div>
            <div class="date">{{ week }}</div>
          </div>
        </div>
        <div class="title-box">
          <span>{{ title }}</span>
        </div>
        <div class="return" @click="$router.back()">
          <img
            src="/static/return.png"
            style="
              width: 26px;
              height: 24px;
              margin-top: 10px;
              margin-left: 11px;
              margin-right: 7px;
            "
          />
          <span>返回</span>
        </div>
      </div>
      <div class="content-box" style="margin: 0">
        <div class="constitute">
          <div class="left-page1">
            <div
              style="
                color: #3496ff;
                margin-left: 25px;
                margin-top: 26px;
                font-size: 18px;
              "
            >
              信息上报情况
            </div>
            <pie-chart1></pie-chart1>
          </div>
          <div class="mid-page">
            <div
              style="
                color: #3496ff;
                margin-left: 25px;
                margin-top: 26px;
                font-size: 18px;
              "
            >
              比学赶超
            </div>
            <bar-chart1></bar-chart1>
          </div>
          <div class="right-page2">
            <div
              style="
                color: #3496ff;
                margin-left: 25px;
                margin-top: 26px;
                font-size: 18px;
              "
            >
              办事进度
            </div>
            <bar-chart2></bar-chart2>
          </div>
        </div>
        <div class="constitute">
          <div class="left-page1">
            <div
              style="
                color: #3496ff;
                margin-left: 25px;
                margin-top: 26px;
                font-size: 18px;
              "
            >
              曝光台
            </div>
            <div
              style="
                color: #00e5ff;
                font-size: 18px;
                margin-top: 2px;
                display: inline-block;
              "
            >
              <div
                v-for="(item, index) in eventData"
                :key="index"
                style="
                  margin-top: 10px;
                  display: inline-block;
                  background-color: #1a2837;
                "
                @click="getEventForm(item.id)"
              >
                <div
                  class="baoguang"
                  style="width: 30px; display: inline-block"
                >
                  {{ index + 1 }}
                </div>
                <div style="width: 350px; display: inline-block">
                  {{ item.eventTitle }}
                </div>
                <div style="display: inline-block; color: #facc14">
                  {{ item.createTime }}
                </div>
              </div>
            </div>
          </div>
          <div class="mid-page">
            <div
              style="
                color: #3496ff;
                margin-left: 25px;
                margin-top: 26px;
                font-size: 18px;
              "
            >
              三个志愿队
            </div>
            <div
              class="zhiyuan"
              style="background-color: #00e676"
              @click="getActiveList()"
            >
              青春白马志愿队
            </div>
            <div
              class="zhiyuan"
              style="background-color: #00e5ff"
              @click="getActiveList()"
            >
              阳光白马志愿队
            </div>
            <div
              class="zhiyuan"
              style="background-color: #ff9100"
              @click="getActiveList()"
            >
              白马铁军志愿队
            </div>
          </div>
          <div class="right-page2">
            <div style="display: inline-block">
              <div
                style="
                  display: inline-block;
                  margin-left: 25px;
                  margin-top: 26px;
                  font-size: 18px;
                  color: #ffffff;
                  padding: 5px;
                  background-color: #4394e4;
                "
              >
                核酸预警
              </div>
            </div>
            <pie-chart3></pie-chart3>
          </div>
        </div>
        <el-dialog :visible.sync="eventDialogVisible" :modal="false">
          <el-form ref="eventForm" :model="eventForm" label-width="80px">
            <el-form-item label="标题">
              <span>{{ eventForm.eventTitle }}</span>
            </el-form-item>
            <el-form-item label="内容">
              <span>{{ eventForm.eventContent }}</span>
            </el-form-item>
            <el-form-item>
              <div v-for="(item, index) in eventForm.images" :key="index">
                <el-image
                  style="width: 100px; height: 100px"
                  :src="
                    'http://47.100.166.173:8099/qujiangqu/api/common/file/download?fileId=' +
                    item
                  "
                  fit="contain"
                >
                </el-image>
              </div>
            </el-form-item>
            <el-form-item label="状态">
              <span>{{ eventForm.status }}</span>
            </el-form-item>
          </el-form>
        </el-dialog>
        <el-dialog :visible.sync="activitylogVisible" :modal="false">
          <el-form ref="eventForm" :model="activeDetail" label-width="80px">
            <el-form-item label="标题">
              {{ activeDetail.activityTitle }}
            </el-form-item>
            <el-form-item label="内容" v-html="html" class="img" v-if="html">
            </el-form-item>
            <el-form-item label="内容" v-else> 无内容 </el-form-item>
          </el-form>
        </el-dialog>
        <el-dialog :visible.sync="zhiyuanDialogVisible" :modal="false">
          <el-table :data="activeList" style="width: 100%">
            <el-table-column prop="volunteerType" label="志愿类型" width="180">
            </el-table-column>
            <el-table-column prop="activityTitle" label="活动名" width="180">
            </el-table-column>
            <el-table-column prop="volunteerUserNames" label="参与人员">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button
                  @click="handleClick(scope.row)"
                  type="text"
                  size="small"
                  >查看</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-dialog>
      </div>
    </dv-full-screen-container>
  </div>
</template>

<script>
import PieChart1 from "../../components/PieChart1/index";
import PieChart3 from "../../components/PieChart3/index";
import BarChart1 from "../../components/BarChart1/index.vue";
import BarChart2 from "../../components/BarChart2/index.vue";
import { initData } from "../../api/inidata";
export default {
  name: "index",
  components: {
    PieChart1,
    BarChart1,
    BarChart2,
    PieChart3,
  },
  data() {
    return {
      zhiyuanDialogVisible: false,
      eventDialogVisible: false,
      activitylogVisible: false,
      loading: true,
      time: "",
      date: "",
      week: "",
      title: "数字白马·基层单元智治中心",
      eventData: [],
      eventForm: {},
      activeList: [],
      activeDetail: {},
      html: undefined, //内容
    };
  },
  created() {
    this.getEventData();
  },
  mounted() {
    this.cancelLoading();
    setInterval(this.getTime, 1000);
  },
  methods: {
    handleClick(row) {
      this.activeDetail = row;
      this.activitylogVisible = true;
      console.log(row);
      this.html = row.activityContent; //渲染
    },
    getActiveList() {
      initData(
        "http://47.100.166.173:8099/qujiangqu/api/volunteer/activity/page",
        { pageIndex: 1, pageSize: 10 }
      ).then((res) => {
        this.activeList = res.data.list;
        this.zhiyuanDialogVisible = true;
      });
    },
    getEventForm(id) {
      initData(
        "http://47.100.166.173:8099/qujiangqu/api/event/view/" + id
      ).then((res) => {
        this.eventForm = res.data;
        if (this.eventForm.eventFileIds) {
          this.eventForm.images = this.eventForm.eventFileIds.split(",");
        }
        this.eventDialogVisible = true;
      });
    },
    getEventData() {
      const params = {
        pageIndex: 1,
        pageSize: 6,
      };
      initData(
        "http://47.100.166.173:8099/qujiangqu/api/event/page",
        params
      ).then((res) => {
        this.eventData = res.data.list;
      });
    },
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    getTime() {
      var date = new Date();
      var YY = date.getFullYear();
      var MM =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var DD = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var hh = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var mm =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var ss =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      var myddy = date.getDay(); //获取存储当前日期
      var weekday = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      this.time = hh + ":" + mm + ":" + ss;

      this.date = YY + "年" + MM + "月" + DD + "号";
      this.week = weekday[myddy];
    },
    go() {
      this.$router.push("/");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../../assets/style/index.scss";
.img >>> p {
  margin: 20px;
  text-indent: 2em;
}

.header-box {
  width: 100%;
  height: 15vh;
  position: relative;

  .return {
    position: absolute;
    top: 5rem;
    right: 200px;
    width: 100px;
    height: 50px;
    border: 2px solid #999999;
    border-radius: 3px;
    display: flex;
    span {
      display: flex;
      font-size: 20px;
      vertical-align: center;
      align-items: center;
    }
  }

  .time-box {
    width: 15%;
    height: 5vh;
    position: absolute;
    top: 5rem;
    left: 3rem;

    > div {
      display: inline-block;
    }

    .time {
      color: #64e5ce;
      font-size: 28px;
      margin-left: 0.2rem;
    }

    .date {
      color: #daf4ef;
      font-size: 12px;
    }
  }

  .title-box {
    position: absolute;
    top: 2.5rem;
    left: 39%;

    span {
      color: #ffffff;
      font-size: 40px;
      font-weight: bold;
      display: flex;
    }
  }
}
.zhiyuan {
  color: #ffffff;
  font-size: 18px;
  width: 400px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 5px;
  margin: 20px 0px 10px 70px;
}
.baoguang {
  margin-top: 4px;
  margin-left: 10px;
  color: #01def8;
}

.content-box {
  height: 100%;
  padding: 10px 30px 0 30px;
  position: relative;
  .name {
    font-size: 20px;
    color: #18a1df;
  }
  .constitute {
    width: 100%;
    height: 40vh;
    //border: 1px solid #257dff;
    display: flex;

    .left-page1 {
      display: inline-block;
      width: 32%;
      margin-left: 1%;
      margin-top: 20px;
      margin-bottom: 20px;
      background-color: rgba(16, 27, 47, 0.8);
    }

    .mid-page {
      display: inline-block;
      width: 32%;
      margin-left: 1%;
      margin-top: 20px;
      margin-bottom: 20px;
      background-color: rgba(16, 27, 47, 0.8);
    }

    .right-page2 {
      display: inline-block;
      width: 32%;
      margin-left: 1%;
      margin-top: 20px;
      margin-bottom: 20px;
      background-color: rgba(16, 27, 47, 0.8);
    }
  }

  .statistics {
    margin-top: 2rem;
    width: 48%;
    height: 35vh;
    float: left;
    //border: 1px solid #257dff;
    .page {
      padding-left: 80px;
      padding-top: 20px;
    }
  }

  .radar {
    margin-top: 2rem;
    width: 48%;
    height: 35vh;
    float: right;
    //border: 1px solid #257dff;
    background-color: rgba(16, 27, 47, 0.8);
  }
}
</style>



