<!--main-->
<template>
  <div>
    <v-chart
      ref="ref_echart1"
      :option="option_column"
      style="height: 300px"
    ></v-chart>
  </div>
</template>
<script>
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
export default {
  data() {
    return {
      option_column: {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {},
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          yAxis: {
            type: 'category',
            data: ['蓝显刚', '廖文章', '邱勇梅', '廖富仙', '朱菊花']
          },
          series: [
            {
              color: '#02cde6',
              height: '5px',
              name: '',
              type: 'bar',
              data: [50, 30,20, 10,28]
            }

          ]
      },
    };
  },
  mounted() {
    window.addEventListener("resize", () => this.$refs.ref_echart1.resize());
  },
  methods: {},
};
</script>



